import React from 'react';

const AxisBottom = ({xScale, innerHeight, tickFormat}) => (
    xScale.ticks(5).map((xTickValues, index) => (
        <g className="tick" key={index} transform={`translate(${xScale(xTickValues)}, 0)`}>
            <line
                key={index}
                x1={0}
                y1={0}
                x2={0}
                y2={innerHeight}
            />
            <text dy=".71em" style={{textAnchor:'middle'}} y={innerHeight+3}>
                {tickFormat(xTickValues)}
            </text>
        </g>
    ))
);

export default React.memo(AxisBottom);