import React from "react";

const ResultSummary = ({
    resultSummary,
    allColors,
    winnersData
}) => {
    return(
            <div className='result-summary'>
            <table className='result-summary-table'>
                <thead>
                    <tr>
                        <td className='table-head-line'>No</td>
                        <td className='table-head-line'>Party</td>
                        <td className='table-head-line'>Party color</td>
                        <td className='table-head-line'>No of seats</td>
                        
                    </tr>
                </thead>
                <tbody>
                    {resultSummary.map(({party, seats}, index) =>{
                        return (
                            <tr key={index}>
                                <td className='table-head-line'>{index+1}</td>
                                <td className='table-head-line'>{party}</td>
                                <td className='table-head-line'>
                                    <svg width={20} height={20}>
                                        <circle
                                            cx={10}
                                            cy={10}
                                            r={9}
                                            fill={allColors[party]}
                                        ></circle>
                                    </svg>
                                </td>
                                <td className='table-head-line'>{seats}</td>
                            </tr>
                        );
                    })}
                    <tr>
                        <td colSpan={3}>Total</td>
                        <td>{winnersData.length}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
};

export default ResultSummary;