
const BarLegend = ({
    className,
    x,
    y
}) => {
    return (
        <>
            <g transform={`translate(${x},${y})`}>
                <rect
                    className={className}
                    x={0}
                    y={0}
                    width={40}
                    height={20}
                ></rect>
                <text
                    className='chart-label'
                    x={100}
                    y={15}
                    textAnchor='middle'
                    >
                    Total votes
                </text>
                <circle
                    className={className}
                    cx={200}
                    cy={10}
                    r={10}
                ></circle>
                <text
                    className='chart-label'
                    x={275}
                    y={15}
                    textAnchor='middle'
                    >
                    Polled votes
                </text>
                <text
                    className='chart-label'
                    x={650}
                    y={15}
                    textAnchor='middle'
                    >
                    Other colored circles represents different party votes
                </text>
            </g>
        </>
    )
};

export default BarLegend;