import React from 'react';

const MarksCircle = ({
    xScale, 
    yScale, 
    rScale, 
    rDomain, 
    xValue, 
    yValue, 
    selectColor,
    allColors, 
    opacity, 
    data
}) => (
    data.map((d, index) => {
        rScale.domain([0, rDomain(d)]);
        return (
            <circle
                key={index}
                cx={xScale(xValue(d))}
                cy={yScale(yValue(d)) + yScale.bandwidth() / 2}
                r={rScale(xValue(d)) / 2}
                fill={allColors[selectColor(d)]}
                opacity={opacity}
            >
            </circle>
        );
    })
);

// export default React.memo(MarksCircle, areEqual);
export default React.memo(MarksCircle);