import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3';

import MarkMap from './MarkMap';
import { 
    yValue,
    party,
    getDataByPartyName,
    getConsInfoByConsNum
 } from '../DataProcess/RearrangeData';

const GeoMapWinner = ({
    district,
    partyName,
    mapData,
    winnersData,
    path,
    fillColor,
    onGeoMapClick
}) => {
    // console.log('GeoMap - Winners');

    const refG = useRef([]);
    // get only the a party from winners data
    const partyWinnersData = getDataByPartyName(winnersData, partyName);

    useEffect(() => {
        const selectMapG = d3.select(refG.current);
        const mapPaths = selectMapG.selectAll('path')

        mapPaths.each(function() {
            const el = d3.select(this);            
            const winnersRowData = getConsInfoByConsNum(winnersData, Number(el.attr('constituency')));
            el.select('title')
                .text(`${yValue(winnersRowData[0])} ${'\n'} ${party(winnersRowData[0])}`);
            el.on('click', (event) => {
                onGeoMapClick({
                    constituency: Number(el.attr('constituency')),
                    mousePos: {x: event.pageX, y: event.pageY}
                });
            });
        });
        
        if((district === 'ALL DISTRICTS') && (partyName === 'All Winning Parties')) {
            mapPaths.style('opacity', 1.0);
        } else if((district !== 'ALL DISTRICTS') || (partyName !== 'All Winning Parties')) {
            mapPaths.each(function() {
                const el = d3.select(this);                
                const rowData = getConsInfoByConsNum(partyWinnersData, Number(el.attr('constituency')));
                if((el.attr('district') === district) || (rowData.length > 0)) {
                    el.style('opacity', 1.0);
                } else {
                    el.style('opacity', 0.4);
                }
            });
        }

    },[district, partyName, mapData])

    return (
        <MarkMap 
            ref={refG}
            className='winners-map'
            mapData={mapData}
            winnersData={winnersData}
            path={path}
            fillColor={fillColor}
        ></MarkMap>
    );
}

export default GeoMapWinner;

