import React from 'react';

import { GetGeoJsonData } from '../DataProcess/GetGeoJsonData';
import GeoChart from './GeoChart';

// const stdMapTopoUrl= 'topo_tn_ac_2021.json';
// const hexMapTopoUrl= 'topo_tn_ac_2021_hex_map.json';

const stdMapTopoUrl= 'https://raw.githubusercontent.com/baskicanvas/tn-ae-2021-result/gh-pages/topo_tn_ac_2021.json';
const hexMapTopoUrl= 'https://raw.githubusercontent.com/baskicanvas/tn-ae-2021-result/gh-pages/topo_tn_ac_2021_hex_map.json';

const GeoChartWrapper = ({
    consData,
    winnersData, 
    district, 
    partyName
}) => {   
    // console.log('GeoChartWrapper');
    const hexMapData = GetGeoJsonData(hexMapTopoUrl, 'hex-map');
    const stdMapData = GetGeoJsonData(stdMapTopoUrl, 'std-map');
    if(!hexMapData && !stdMapData) {
        return <h1>Loading...</h1>
    }
    return (
        <GeoChart
            hexMapData={hexMapData}
            stdMapData={stdMapData}
            consData={consData}
            winnersData={winnersData}
            district={district}
            partyName={partyName}
        ></GeoChart>
    );
};

export default GeoChartWrapper;