import { useEffect, useState } from "react";
import * as d3 from 'd3';

export const GetCsvData = (csvUrl) => {

    const [data, setData] = useState(null);

    // Hooks useEffect will be at each render process
    // with '[]' dependencies it will be called only once
    useEffect(() => {
        d3.csv(csvUrl, d3.autoType).then(data => {
            setData(data);
        });
    }, []);
    return data;
};
