import { useEffect, useState } from "react";
import * as d3 from 'd3';
import * as topojson from "topojson-client";

export const GetGeoJsonData = (jsonUrl, mapType) => {
    const [data, setData] = useState(null);
    // console.log('GetGeoJsonData');
    // Hooks useEffect will be at each render process
    // with '[]' dependencies it will be called only once
    useEffect(() => {
        d3.json(jsonUrl).then(topojsonData => {
            switch(mapType) {
               case 'hex-map':
                    const {tn_ac_2021_hex_map} = topojsonData.objects
                    setData({
                        tn_ac: topojson.feature(topojsonData, tn_ac_2021_hex_map),
                        interiors: topojson.mesh(topojsonData, tn_ac_2021_hex_map, (a,b) => a !== b)
                    });
                   break;
                case 'std-map':
                    const {tn_ac_2021} = topojsonData.objects
                    setData({
                        tn_ac: topojson.feature(topojsonData, tn_ac_2021),
                        interiors: topojson.mesh(topojsonData, tn_ac_2021, (a,b) => a !== b)
                    });
                    break;
                default:
                    setData(null);
            }
        });
    }, []);
    return data;
};

