import { useEffect, useRef } from "react";
import * as d3 from 'd3';

import GeoMapLegend from './GeoMapLegend';
import MarkMap from "./MarkMap";

import { 
    yValue,
    getDataByPartyName,
    getConsInfoByConsNum,
    candidateVotes,
    getConsDataForIndeCandidates
 } from "../DataProcess/RearrangeData";

const GeoMapParties = ({
    selectParty,
    mapData,
    winnersData,
    consData,
    path,
    fillColor,
    onGeoMapClick
}) => {
    // console.log('GeoMap - Parties');

    const refG = useRef([]);
    const refGeoMapLegengG = useRef();

    const steps = 5;

    useEffect(() => {
        let partyData = 0;
        let mapLegendText = 0;
        let mapLegendOpacity = 0;

        if(selectParty === 'Independent') {
            partyData = getConsDataForIndeCandidates(consData, winnersData)
        } else {
            partyData = getDataByPartyName(consData, selectParty);
        }
        const selectMapG = d3.select(refG.current);
        const mapPaths = selectMapG.selectAll('path')

        const selectlegengG = d3.select(refGeoMapLegengG.current);
        const mapLegendsText = selectlegengG.selectAll('text');
        const mapLegendsCircle = selectlegengG.selectAll('circle');

        mapPaths.each(function() {
            const el = d3.select(this)
            const rowData = getConsInfoByConsNum(partyData, Number(el.attr('constituency')))
            if(rowData.length > 0) {
                const color = fillColor(selectParty);
                el.attr('fill', color);
                el.style('fill-opacity', candidateVotes(rowData[0]) / 100);
                el.attr('stroke', fillColor(selectParty));
                el.select('title')
                    .text(`${yValue(rowData[0])} ${'\n'} ${candidateVotes(rowData[0])}%`)
            } else {
                const rowData = getConsInfoByConsNum(winnersData, Number(el.attr('constituency')))
                el.attr('fill', '#000000');
                el.attr('stroke', fillColor(selectParty));
                el.style('fill-opacity', 0.1)
                el.select('title')
                    .text(`${yValue(rowData[0])}`)
            }
            el.on('click', (event) => {
                onGeoMapClick({
                    constituency: Number(el.attr('constituency')),
                    mousePos: {x: event.pageX, y: event.pageY}
                });
            });
        });

        mapLegendsText.each(function() {
            const lgel = d3.select(this);
            if(mapLegendText <= 100) {
                lgel.text(`${mapLegendText}%`);
                mapLegendText = mapLegendText + 20;
            }else {
                lgel.text('Not contested');
            }

            
        });

        mapLegendsCircle.each(function() {
            const lgel = d3.select(this);
            if(mapLegendOpacity <= 1.0) {
                lgel.attr('fill', fillColor(selectParty));
                lgel.style('fill-opacity', mapLegendOpacity);
                mapLegendOpacity = mapLegendOpacity + 0.2;
            }else {
                lgel.attr('fill', '#000000');
                lgel.style('fill-opacity', 0.1);
            }
        });
    },[selectParty, mapData, consData, winnersData, fillColor])

    return (
        <>
        <GeoMapLegend
            ref={refGeoMapLegengG}
            steps={steps}
            additionalSteps={1}
            fillColor={fillColor}
        ></GeoMapLegend>
        <MarkMap 
            ref={refG}
            className='parties-map'
            mapData={mapData}
            winnersData={winnersData}
            path={path}
            fillColor={fillColor}
        ></MarkMap>
        </>
    );
}

export default GeoMapParties;

