import React from 'react';

const MarksBar = ({
    cName, 
    xScale, 
    yScale,
    getConsNumber,
    innerWidth, 
    xValue, 
    yValue, 
    data,
    onMarksBarClick
}) => {
    // console.log('MarksBar');
    return(
        data.map((d, index) => {
            return (
                <g key={yValue(d)} transform={`translate(${xScale(innerWidth / 2)}, 0)`}>
                    <rect
                        className={cName}
                        x={0}
                        y={yScale(yValue(d))}
                        width={xScale(xValue(d)) - xScale(innerWidth / 2)}
                        height={yScale.bandwidth()}
                        onClick={() => onMarksBarClick(getConsNumber(d))}
                    />
                </g>
            );
        })
    );
};

function areEqual(prevProps, nextProps) {
    return prevProps.cName === nextProps.cName
        && prevProps.xScale === nextProps.xScale
        && prevProps.yScale === nextProps.yScale
        && prevProps.innerWidth === nextProps.innerWidth
        && prevProps.xValue === nextProps.xValue
        && prevProps.yValue === nextProps.yValue
        && prevProps.data === nextProps.data
}

export default React.memo(MarksBar, areEqual);
