import React from 'react';

const Dropdown = ({
    id,
    className,
    defaultValue,
    options,
    dropdownVale,
    disabled,
    onOptionChange
}) => {
    // console.log('Dropdown');
    return (
        <div>
            <select 
                id={id} 
                defaultValue={defaultValue}
                className='select-option' 
                disabled = {disabled}
                onChange={event => onOptionChange(event.target.value)}
            >
                {options.map(({value, label}, index) => (
                    <option 
                        key={index} 
                        value={value}
                        defaultValue={value === dropdownVale}
                    >{label}</option>
                ))}                   
            </select>
        </div>
    );
};

export default Dropdown;