import React from 'react';

const AxisLeft = ({yScale, innerWidth}) => (
    yScale.domain().map((yTickValues, index) => (
        <g className="tick" key={index} transform={`translate(0, ${yScale(yTickValues) + yScale.bandwidth() / 2})`}>
            <text dy=".32em"x={innerWidth / 5 - 5} style={{textAnchor:'end'}}>
                {yTickValues}
            </text>
        </g>
    ))
);

export default React.memo(AxisLeft);