import React from "react";

const GeoMapLegend = React.forwardRef(({
    steps,
    additionalSteps,
    fillColor
}, ref) => {
    let posX = 20;
    let posY = 0;
    const r = 10;
    const items = [];
    for(let i=0; i<=(steps + additionalSteps); i++) {
        items.push(i);
    }
    return(
        <g ref={ref}>
            {items.map((d, index) => {
                posY = posY + 25;
                return(
                    <>
                    <circle
                        cx={posX}
                        cy={posY}
                        r={r}
                        fill={fillColor()}
                    ></circle>
                    <text
                        x={posX + r + 5}
                        y={posY + r/2}
                    ></text>
                    </>
                );
            })}
        </g>
    );
});

export default GeoMapLegend;