import React, { useEffect, useRef } from 'react'
import * as d3 from 'd3'

import GeoMapLegend from './GeoMapLegend';
import MarkMap from './MarkMap';
import { 
    yValue,
    xValueVotes,
    xValuePolledVotes,
    getPollPercentage,
    getConsInfoByConsNum
} from '../DataProcess/RearrangeData';

const GeoMapVotes = ({
    voteMap,
    mapData,
    winnersData,
    path,
    fillColor,
    onGeoMapClick
}) => {
    // console.log('GeoMap - Votes');

    const refG = useRef([]);
    const refGeoMapLegengG = useRef();

    const steps = 5;
    const dataFormat = d3.format(",d");

    const df = d3.format(".2%")
    const df2 = d3.format(".2f")

    useEffect(() => {
        let maxVotes = 1.0;
        let opacity = 1.0;
        let mapLegendText = 0;
        let mapLegendOpacity = 0;

        const selectMapG = d3.select(refG.current);
        const mapPaths = selectMapG.selectAll('path')

        const selectlegengG = d3.select(refGeoMapLegengG.current);
        const mapLegendsText = selectlegengG.selectAll('text');
        const mapLegendsCircle = selectlegengG.selectAll('circle');

        mapPaths.each(function() {
            const el = d3.select(this)
            const rowData = getConsInfoByConsNum(winnersData, Number(el.attr('constituency')))
            switch (voteMap) {
                case 'candidates':
                    const candidates = rowData[0]['candidates'];
                    opacity = candidates / 100;
                    el.select('title')
                        .text(`${yValue(rowData[0])} ${'\n'} ${candidates}`);
                    break;
                case 'total_votes':
                    maxVotes = d3.max(winnersData.map(xValueVotes));
                    opacity = rowData[0][voteMap] / maxVotes;
                    el.select('title')
                        .text(`${yValue(rowData[0])} ${'\n'} ${dataFormat(xValueVotes(rowData[0]))}`);
                    break;
                case 'total_polled_votes':
                    maxVotes = rowData[0]['total_votes'];
                    opacity = rowData[0][voteMap] / maxVotes;
                    el.select('title')
                        .text(`${yValue(rowData[0])} ${'\n'} ${dataFormat(xValuePolledVotes(rowData[0]))} (${df(getPollPercentage(rowData[0]))})`);
                    break;
                case 'winning_margin':
                    maxVotes = xValuePolledVotes(rowData[0]);
                    const winningMargin = rowData[0]['winning_margin'];
                    opacity = winningMargin / maxVotes;
                    el.select('title')
                        .text(`${yValue(rowData[0])} ${'\n'} ${dataFormat(winningMargin)} (${df2(rowData[0]['winning_margin_percentage'])}%)`);
                    break;
                default:
                    opacity = 1.0;
            }
            el.style('fill', d3.interpolateGnBu(opacity));
            el.on('click', (event) => {
                onGeoMapClick({
                    constituency: Number(el.attr('constituency')),
                    mousePos: {x: event.pageX, y: event.pageY}
                });
            });
        });

        mapLegendsText.each(function() {
            const lgel = d3.select(this);
            if(voteMap === 'candidates') {
                lgel.text(mapLegendText);
                mapLegendText = mapLegendText + 20;
            }else {
                lgel.text(`${mapLegendText}%`);
                    mapLegendText = mapLegendText + 20;
            }
        });

        mapLegendsCircle.each(function() {
            const lgel = d3.select(this);
            lgel.attr('fill', d3.interpolateGnBu(mapLegendOpacity));
            mapLegendOpacity = mapLegendOpacity + 0.2;
        });
    },[voteMap, mapData])
    return (
        <>
        <GeoMapLegend
            ref={refGeoMapLegengG}
            steps={steps}
            additionalSteps={0}
            fillColor={fillColor}
        ></GeoMapLegend>
        <MarkMap 
            ref={refG}
            className='votes-map'
            mapData={mapData}
            winnersData={winnersData}
            path={path}
            fillColor={fillColor}
        ></MarkMap>
        </>
    );
}

export default GeoMapVotes;