import React from "react";

const ElectionSummary = ({
    electionSummary
}) => {
    return(
        <div className='election-summary'>
            <table className='election-summary-table'>
                <tbody>
                    {electionSummary.map(({key, value}, index) => {
                        return (
                            <tr key={index}>
                                <td className='table-head-line'>{key}</td>
                                <td className='table-head-line'>{value}</td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
        </div>
    );
};

export default ElectionSummary;

