import React from 'react';

const GeoMapTooltip = ({
    xValueVotes,
    xValuePolledVotes,
    xValueCandidateVotes,
    yValue,
    party,
    candidateVotes,
    selectColor,
    allColors,
    candidate,
    mouseConsInfo,
    dataFormat,
    onCloseBtnClick
}) => {
    if(mouseConsInfo === undefined) {
        // console.log(consInfo, tooltip);
        return null;
    }
    // console.log('Tooltip');
    const {consInfo, mousePos} = mouseConsInfo;
    // the hard coded offset values are from CSS
    const tooltipXPos = mousePos['x'] + 7;
    const tooltipYPos = mousePos['y'] - 15 - 7;
    const transform = {
        transform: `translate(${tooltipXPos}px, calc(${tooltipYPos}px))`
    }
    return (
        <div 
            className='geo-map-tooltip' 
            style={transform}
        >
            <table>
                <thead className='table-head'>
                    <tr>
                        <td className='table-head-name' colSpan={5}>{yValue(consInfo[0])}</td>
                        <td className='table-head-name'>
                            <button onClick={() => {
                                return onCloseBtnClick(undefined)
                                }}
                            >X
                            </button>
                        </td>
                    </tr>
                    <tr>
                        <td colSpan={4}>Total Votes</td>
                        <td colSpan={2}>{dataFormat(xValueVotes(consInfo[0]))}</td>
                    </tr>
                    <tr>
                        <td className='table-head-line' colSpan={4}>Total Polled Votes</td>
                        <td className='table-head-line' colSpan={2}>{dataFormat(xValuePolledVotes(consInfo[0]))}</td>
                    </tr>
                    <tr>
                        <td className='table-head-line'>No</td>
                        <td className='table-head-line'>Name</td>
                        <td className='table-head-line'>Party</td>
                        <td className='table-head-line'>Votes</td>
                        <td className='table-head-line'>%</td>
                        <td className='table-head-line'></td>
                    </tr>
                </thead>
                <tbody>
                    {consInfo.map((d, index) => {
                        return (
                            <tr key={index}>
                                <td>{index + 1}</td>
                                <td>{candidate(d)}</td>
                                <td>{party(d)}</td>
                                <td>{dataFormat(xValueCandidateVotes(d))}</td>
                                <td>{candidateVotes(d)}</td>
                                <td>
                                    <svg width={20} height={20}>
                                        <circle
                                            cx={10}
                                            cy={10}
                                            r={9}
                                            fill={allColors[selectColor(d)]}
                                            opacity={candidateVotes(d)}
                                        ></circle>
                                    </svg>
                                </td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
        </div>
    );
    
};

export default React.memo(GeoMapTooltip);
// export default Tooltip;