import React from "react";

const DisclaimerSection = ({
    className
}) => {
    return(
        <>
        <hr></hr>
        <div className={className}>
            <p>
                The election data sets are collected from election commission websites on 03/07/2021
            </p>
            <ol>
                <li>
                    <a href='https://results.eci.gov.in/Result2021/partywiseresult-S22.htm?st=S22' target="_blank" rel="noopener noreferrer">
                        Tamil Nadu General Assembly Elections MAY-2021
                    </a>
                </li>
                <li>
                     <a href='https://www.elections.tn.gov.in/acwithcandidate/' target="_blank" rel="noopener noreferrer">
                        District wise assembly consituency electroate and candidates details.
                    </a>
                </li>
                <li>
                     <a href='https://eci.gov.in/files/file/13680-tamil-nadu-general-legislative-election-2021/' target="_blank" rel="noopener noreferrer">
                     Tamil Nadu General Legislative Election 2021.
                    </a>
                </li>
            </ol>
            <p>
                As on 09/09/2021, the election result data is not available from <a href='https://www.elections.tn.gov.in/acwithcandidate/' target="_blank" rel="noopener noreferrer">[ 1 ]</a>.
                The updated election result data is available from <a href='https://eci.gov.in/files/file/13680-tamil-nadu-general-legislative-election-2021/' target="_blank" rel="noopener noreferrer">[ 3 ]</a>.
                Comparing with <a href='https://www.elections.tn.gov.in/acwithcandidate/' target="_blank" rel="noopener noreferrer">[ 1 ]</a>
                there are some minor difference in the data set which was not considered.
            </p>
            <p>
                The asscembly constituency boundary map files are sourced from <a href='http://projects.datameet.org/maps/assembly-constituencies/#download' target="_blank" rel="noopener noreferrer">[ 4 ]</a>
                and converted to TopoJSON using <a href='https://mapshaper.org/' target="_blank" rel="noopener noreferrer">[ 6 ]</a>.
                Used <a href ='https://qgis.org/en/site/' target="_blank" rel="noopener noreferrer">QGIS</a> to create the <a href='https://github.com/BaskaranKa/tn-hex-tile-map' target="_blank" rel="noopener noreferrer"> hex-tile map</a> with similar attribute names as in the boundary map file.

            </p>
            <ol start='4'>
                <li>
                     <a href='http://projects.datameet.org/maps/assembly-constituencies/#download' target="_blank" rel="noopener noreferrer">
                     India - Assembly Constituencies
                    </a>
                </li>
                <li>
                     <a href='https://github.com/datameet/maps/tree/master/assembly-constituencies' target="_blank" rel="noopener noreferrer">
                     GitHub repository for India - Assembly Constituencies
                    </a>
                </li>
                <li>
                     <a href='https://mapshaper.org/' target="_blank" rel="noopener noreferrer">
                     mapshaper for viewing and converting geospatical data
                    </a>
                </li>
            </ol>
        </div>
        </>
    );
};

export default DisclaimerSection;