import React from 'react'

const MarkMap = React.forwardRef(({
    className,
    mapData: {tn_ac, interiors},
    winnersData,
    path,
    fillColor
}, ref) => {
    // console.log('MarkMap');
    return (
        <g ref={ref} className={className}>
            {tn_ac.features.map((feature, index) => {
                const acNumber = feature['properties']['AC_NO'];
                const distNumber = feature['properties']['DT_CODE'];
                return (
                    <>
                    <path
                        key={index}
                        className={'land'} 
                        district={distNumber}
                        constituency={acNumber}
                        d={path(feature)}
                        fill={fillColor(winnersData, acNumber)}
                    >
                        <title className='geo-tooltip'>
                        </title>
                    </path>
                    </>
                );
            })}
        </g>
    );
});


function areEqual (prevProps, nextProps) {
    return prevProps.mapData === nextProps.mapData;
}

export default React.memo(MarkMap, areEqual);
