import React from "react"

const RadioButtonGroup = ({
    className,
    onChangeValue
}) => {
    return(
        <div className={className} onChange={(event)=>onChangeValue(event.target.value)}>
            <RadioButton
                id={'hextile-map'}
                name={'map-type'}
                checked={true}
                value={'hextile-map'}
                text={'Hex tile map'}
            ></RadioButton>
            <RadioButton
                id={'standard-map'}
                name={'map-type'}
                checked={false}
                value={'standard-map'}
                text={'Standard map'}
            ></RadioButton>
        </div>
    );
};


const RadioButton = ({
    id,
    name,
    checked,
    value,
    text
}) => {
    return(
        <>
        <input 
            type='radio' 
            id={id} 
            name={name} 
            defaultChecked={checked} 
            value={value}
        ></input>
        <label htmlFor={id}>{text}</label>
        </>
    );
};

export default RadioButtonGroup;


