import React, { useState } from 'react';
import * as d3 from 'd3';

import GeoMapWinner from './GeoMapWinner';
import GeoMapWinnerLegend from './GeoMapWinnerLegend';
import GeoMapVotes from './GeoMapVotes';
import GeoMapParties from './GeoMapParties';
import Dropdown from '../components/Dropdown';
import SubtitleH3 from '../components/SubtitleH3';
import TextContent from '../components/TextContent';
import RadioButtonGroup from '../components/RadioButtonGroup';
import GeoMapTooltip from './GeoMapTooltip';

import {
    xValueVotes,
    xValuePolledVotes,
    xValueCandidateVotes,
    yValue,
    candidateVotes,
    selectColor,
    candidate,
    getDescenConsInfo,
    getConsNumber,
    makePartiesDropdownData,
    getWinnersSummary,
    allColors,
    party,
    voteOptions
} from '../DataProcess/RearrangeData';

const width = 900;
const height = 900;
const mapScale = 8000;
const TamilNadu = [78.5, 10.5];

const projection = d3.geoEquirectangular()
        .center(TamilNadu)
        .scale(mapScale)
        .translate([width/2, height/1.8]);
const path = d3.geoPath(projection);

const GeoChart = ({
    hexMapData,
    stdMapData,
    consData,
    winnersData,
    district,
    partyName
}) => {
    // console.log('GeoChart');
    const [mapType, setMapType] = useState('hextile-map');
    const [voteMap, setVoteMap] = useState('candidates');
    const [selectParty, setSelectParty] =useState('Dravida Munnetra Kazhagam');
    const [mouseConsInfo, setMouseConsInfo] = useState(undefined);
    // console.log('mouseConsInfo: ', mouseConsInfo);

    let mapData = 0;
    if(mapType === 'hextile-map') {
        mapData = hexMapData;
    } else {
        mapData = stdMapData;
    }
    const fillColorWinners = (winnersData, acNumber) => {
        const rowData = winnersData.find(d => getConsNumber(d) === acNumber)
        return allColors[party(rowData)];
    };

    const fillColorVotes = () => {
            // return allColors['total_polled_votes'];
            return allColors['votes_map_color'];
    };

    const fillColorParties = (selectParty) => {
        if(allColors[selectParty]) {
            return allColors[selectParty];
        }
        return allColors['others'];
    };

    const resultSummary = getWinnersSummary(winnersData);
    const allParties = makePartiesDropdownData(consData, 'party');

    return (
        <>
        <SubtitleH3
            className='subtitle'
            text={'Winners Map'}
        ></SubtitleH3>
        <TextContent
            className='text-content'
            textContent={
                `Winning parties overview. The hex tile map better represents the data set.`
            }
        ></TextContent>
        <RadioButtonGroup
            className={'margin-tb'}
            onChangeValue={setMapType}
        ></RadioButtonGroup>

        <div>
            <svg width={width} height={height}>
                <GeoMapWinnerLegend
                    resultSummary={resultSummary}
                ></GeoMapWinnerLegend>
                <GeoMapWinner
                    district={district}
                    partyName={partyName}
                    mapData={mapData}
                    winnersData={winnersData}
                    path={path}
                    fillColor={fillColorWinners}
                    onGeoMapClick={mouseInfo => {
                        setMouseConsInfo({
                            consInfo: getDescenConsInfo(consData, mouseInfo['constituency']),
                            mousePos: mouseInfo['mousePos']
                        });
                    }}
                ></GeoMapWinner>
            </svg>
        </div>

        <SubtitleH3
            className='subtitle'
            text={`Constituencies Information Map`}
        ></SubtitleH3>
        <TextContent
            className='text-content'
            textContent={
                `Constituency information view for total number of candidates contested, total polled votes, total votes and
                winning margin.`
            }
        ></TextContent>
        {/* <div className='geo-chart-container'> */}
            <div>
                <div className='select-option-container'>
                    <Dropdown
                        id={'select-votes'}
                        className={'select-votes'}
                        defaultValue={'candidates'}
                        options={voteOptions}
                        defaultVale={voteMap}
                        disabled={false}
                        onOptionChange={setVoteMap}
                    ></Dropdown>
                </div>
                <div>
                    <svg width={width} height={height}>
                        <GeoMapVotes 
                            voteMap={voteMap}
                            mapData={mapData}
                            winnersData={winnersData}
                            path={path}
                            fillColor={fillColorVotes}
                            onGeoMapClick={mouseInfo => {
                                setMouseConsInfo({
                                    consInfo: getDescenConsInfo(consData, mouseInfo['constituency']),
                                    mousePos: mouseInfo['mousePos']
                                });
                            }}
                        ></GeoMapVotes>
                    </svg>
                </div>
            </div>

            <SubtitleH3
                className='subtitle'
                text={`Vote sharing Map`}
            ></SubtitleH3>
            <TextContent
                className='text-content'
                textContent={
                    `The vote shating map only includes parties who has secured minimum one percentage of votes atleast in one constituency.`
                }
            ></TextContent>
            <div>
                <div className='select-option-container'>
                    <Dropdown
                        id={'select-parties'}
                        className={'select-parties'}
                        defaultValue={'Dravida Munnetra Kazhagam'}
                        options={allParties}
                        defaultVale={selectParty}
                        disabled={false}
                        onOptionChange={setSelectParty}
                    ></Dropdown>
                </div>
                <div>
                    <svg width={width} height={height}>
                        <GeoMapParties 
                            selectParty={selectParty}
                            mapData={mapData}
                            winnersData={winnersData}
                            consData={consData}
                            path={path}
                            fillColor={fillColorParties}
                            onGeoMapClick={mouseInfo => {
                                setMouseConsInfo({
                                    consInfo: getDescenConsInfo(consData, mouseInfo['constituency']),
                                    mousePos: mouseInfo['mousePos']
                                });
                            }}
                        ></GeoMapParties>
                    </svg>
                </div>
            </div>
            <GeoMapTooltip
                xValueVotes={xValueVotes}
                xValuePolledVotes={xValuePolledVotes}
                xValueCandidateVotes={xValueCandidateVotes}
                yValue={yValue}
                party={party}
                candidateVotes={candidateVotes}
                selectColor={selectColor}
                allColors={allColors}
                candidate={candidate}
                mouseConsInfo={mouseConsInfo}
                dataFormat={d3.format(",d")}
                onCloseBtnClick={setMouseConsInfo}
            /> 
        {/* </div> */}
        </>
    );  
}

export default GeoChart;