import React from "react";

import { allColors } from "../DataProcess/RearrangeData";

const GeoMapWinnerLegend = ({
    resultSummary
}) => {
    let posX = 20;
    let posY = 0;
    const r = 10;
    return (
        <g>
            {resultSummary.map((d, index) => {
                posY = posY + 25;
                return(
                    <>
                    <circle
                        key={d['party']}
                        cx={posX}
                        cy={posY}
                        r={r}
                        fill={allColors[d['party']]}
                    ></circle>
                    <text
                        key={index}
                        x={posX + r + 5}
                        y={posY + r/2}
                    >{d['party']}
                    </text>
                    </>
                );
            })}
        </g>
    );
};

export default GeoMapWinnerLegend;