import React, { useEffect, useState } from 'react';
import * as d3 from 'd3';

import AxisBottom from './AxisBottom';
import AxisLeft from './AxisLeft';
import MarksBar from './MarksBar';
import MarksCircle from './MarksCircle';
import BarTooltip from './BarTooltip';
import BarLegend from './BarLegend';

import { 
    xValueVotes,
    xValuePolledVotes,
    xValueCandidateVotes,
    yValue,
    getConsNumber,
    party,
    candidateVotes,
    selectColor,
    getPolledVotesColor,
    candidate,
    getConsNumbers,
    getDataByDistrictNum,
    getDataByPartyName,
    getFilterdDataByConsNum,
    getDescenConsInfo,
    sortDescenting,
    allColors
} from '../DataProcess/RearrangeData';


const width = 1080;
const margin = {top: 30, right: 20, bottom: 65, left: 20};
const innerWidth = width - margin.left - margin.right;

    
const BarChart = ({
    data,
    winnersData,
    district,
    chartOrder,
    partyName
}) => {
    const [consData, setconsData] = useState(undefined);
    const {
        xScale, 
        yScale,
        rScale,
        innerHeight,
        height,
        uniqueAcData,
        allDataSort
    } = React.useMemo(() => {
        if (!data) return {}
        // console.log('re-ran-L45');
        let uniqueData = {};
        let uniqueAcData = {};
        let allDataSort = {};

        uniqueData = winnersData;
        uniqueAcData = uniqueData;
        allDataSort = data;

        if(district !== 'ALL DISTRICTS') {
            uniqueData = getDataByDistrictNum(winnersData, district);
            uniqueAcData = uniqueData;
            allDataSort = getDataByDistrictNum(data, district);
        }

        if(partyName !== 'All Winning Parties') {
            uniqueData = getDataByPartyName(winnersData, partyName);
            uniqueAcData = uniqueData;
            const consNumbers = getConsNumbers(uniqueData);
            allDataSort = getFilterdDataByConsNum(data, consNumbers);
        }
        
        allDataSort.map(d => d['poll_percentage'] = xValuePolledVotes(d)/xValueVotes(d));

        
        // svg height based on data length
        const height = 30 * uniqueData.length + margin.top + margin.bottom;
        const innerHeight = height - margin.top - margin.bottom;

        //  sorting the data based on the select chart sorting order
        if(chartOrder !== 'default'){       
            const uniqueDataSort = sortDescenting(uniqueData, chartOrder);
            uniqueAcData = uniqueDataSort;
        }

        const xScale = d3.scaleLinear()
            .domain([0, d3.max(winnersData.map(xValueVotes))])
            .range([innerWidth / 5, innerWidth]);
        
        const yScale = d3.scaleBand()
            .domain(uniqueAcData.map(yValue))
            .range([0, innerHeight])
            .padding(0.10);
        
        const rScale = d3.scaleLinear()
            .range([0, yScale.bandwidth()]);
        
        return {
            xScale, 
            yScale,
            rScale,
            innerHeight,
            height,
            uniqueAcData,
            allDataSort
        }
    },[data, winnersData, district, chartOrder, partyName]);

    // resets (close) the bar tooltip
    useEffect(() => {
        setconsData(undefined);
    }, [district, chartOrder, partyName]);

    if(!data) {
        return <h1>Loading...</h1>
    }
    
    return (
        <div id='wrapper' className='wrapper'>
            <svg width={width} height={height}>
                <g transform={`translate(${0},${margin.top})`}>
                    <BarLegend
                        className='total-votes'
                        x={50}
                        y={-25}
                    ></BarLegend>

                    <g>
                    <AxisBottom 
                        xScale={xScale} 
                        innerHeight={innerHeight}
                        tickFormat={d3.format(",d")}
                    />
                    </g>

                    <g>
                        <AxisLeft yScale={yScale} innerWidth={innerWidth}/>
                    </g>
                    
                    <MarksBar
                        cName='total-votes'
                        xScale={xScale} 
                        yScale={yScale} 
                        innerWidth={innerWidth}
                        xValue={xValueVotes}
                        yValue={yValue}
                        getConsNumber={getConsNumber}
                        data={uniqueAcData}
                        onMarksBarClick={consNumber => 
                            setconsData(getDescenConsInfo(allDataSort, consNumber))
                        }
                    />

                    {/* for total polled votes */}
                    <g>
                        <MarksCircle 
                            xScale={xScale}
                            yScale={yScale} 
                            rScale={rScale}
                            rDomain={xValuePolledVotes}
                            xValue={xValuePolledVotes}
                            yValue={yValue}
                            selectColor={getPolledVotesColor}
                            allColors={allColors}
                            opacity={0.1}
                            data={uniqueAcData}
                        />
                    </g>

                    {/* for party votes */}
                    <g>
                        <MarksCircle 
                            xScale={xScale}
                            yScale={yScale} 
                            rScale={rScale}
                            rDomain={xValuePolledVotes}
                            xValue={xValueCandidateVotes}
                            yValue={yValue}
                            selectColor={selectColor}
                            allColors={allColors}
                            opacity={0.9}
                            data={allDataSort}
                        />
                    </g>
                </g>
            </svg>
            <BarTooltip
                margin={margin}
                xScale={xScale}
                yScale={yScale}
                xValueVotes={xValueVotes}
                xValuePolledVotes={xValuePolledVotes}
                yValue={yValue}
                party={party}
                xValueCandidateVotes={xValueCandidateVotes}
                candidateVotes={candidateVotes}
                selectColor={selectColor}
                allColors={allColors}
                candidate={candidate}
                consData={consData}
                dataFormat={d3.format(",d")}
                onCloseBtnClick={setconsData}
            /> 
        </div>
    );
};

export default BarChart