let df = d3.format(",d");
// all accessor function
//  Get the value in a given row
// Example xValueVotes(d) will give the 'toral_value' in that row
export const xValueVotes = d => d['total_votes'];
export const xValuePolledVotes = d => d['total_polled_votes'];
export const getPollPercentage = d => d['poll_percentage']
export const xValueCandidateVotes = d => d['candidate_total_votes'];
export const yValue = d => d['constituency'];
export const getConsNumber = d => d['constituency_number'];
const getDistrict = d => d['district'];
const getDistrictNumber = d => d['district_number'];
export const selectColor = d => d['party'];
export const getPolledVotesColor = d => 'total_polled_votes';
export const candidate = d => d['candidate']
export const party = d => d['party']
export const candidateVotes = d => d['candidate_vote_%']

export const getUniqueData = (data) => [...new Map(data.map(rowData =>
    [rowData['constituency_number'], rowData])).values()];

// filters all the rows belongs to the given constituency
const getConsInfoByConsName = (data, consName) => (
    data.filter(d => (yValue(d) === consName))
);

export const getConsInfoByConsNum = (data, consNumber) => (
    data.filter(d => (getConsNumber(d) === consNumber))
);

export const getDataByDistrictNum = (data, dustrictNum) => (
    data.filter(d => (getDistrictNumber(d) === +dustrictNum))
);

export const getDataByPartyName = (data, partyName) => (
    data.filter(d => (party(d) === partyName))
);

// get cons info in decending order
export const getDescenConsInfo = (data, consNumber) => {
    return sortDescenting(getConsInfoByConsNum(data, consNumber), 'candidate_total_votes');
};

const getDescenConsInfoThroughConsNum = (data, consNumber) => {
    return sortDescenting(getConsInfoByConsNum(data, consNumber), 'candidate_total_votes');
};

// sorting functions (input is dataset and the column header)
export const sortDescenting = (data, key) => (
    data.slice().sort((a, b) => d3.descending(a[key], b[key]))
);

const sortAscending = (data, key) => (
    data.slice().sort((a, b) => d3.ascending(a[key], b[key]))
);

export const getConsNumbers = (data) => [...new Map(data.map(rowData =>
    [rowData['constituency_number'], 0])).keys()];

const getUniqueDataByKey = (data, key) => [...new Map(data.map(rowData =>
    [rowData[key], rowData])).values()];
    
export const getUniqueSetbyColumnKey = (data, key) => [...new Set(data.map(rowData => rowData[key]))]

export const makeDistrictsDropdownData = (data) => {
    const uniqueData = getUniqueDataByKey(data, 'district_number');
    const sortUniqueData = sortAscending(uniqueData, 'district');
    const dropdownData = [...sortUniqueData.map(d => ({value: getDistrictNumber(d), label: getDistrict(d)}))]
    dropdownData.unshift({value:'ALL DISTRICTS', label: 'ALL DISTRICTS'});
    return dropdownData;
}

export const makeWinPartiesDropdownData = (data) => {
    const uniqueData = getUniqueDataByKey(data, 'party');
    const sortUniqueData = sortAscending(uniqueData, 'party');
    const dropdownData = [...sortUniqueData.map(d => ({value: party(d), label: party(d)}))]
    dropdownData.unshift({value:'All Winning Parties', label: 'All Winning Parties'});
    return dropdownData;
}

export const makePartiesDropdownData = (data) => {
    const filterData = data.filter(rowData => candidateVotes(rowData) >= 1.0)
    const partiesAbove = [...new Set(filterData.map(rowData => party(rowData)))];
    const partiesAboveSort = partiesAbove.slice().sort((a, b) => d3.ascending(a, b));
    const dropdownData = [...partiesAboveSort.map(d => ({value: d, label: d}))]
    return dropdownData;
}

export const getElectionSummary = (data, winnersData) => {
    const electionSummary = [];
    let totalParties = 0;
    let maleVotes = 0;
    let femaleVotes = 0;
    let otherVotes = 0;
    let totalVotes = 0;
    let totalPolledVotes = 0;
    let districts = 0;

    totalParties = getUniqueSetbyColumnKey(data, 'party');
    districts = getUniqueSetbyColumnKey(winnersData, 'district_number');
    for(let rowData of winnersData) {
        maleVotes += rowData['male_votes'];
        femaleVotes += rowData['female_votes'];
        otherVotes += rowData['thirunangai_votes'];
        totalVotes += rowData['total_votes'];
        totalPolledVotes += rowData['total_polled_votes'];        
    }
    
    electionSummary.push(
        {key: 'Total districts', value: df(districts.length)},
        {key: 'Total constituency', value: df(winnersData.length)},
        {key: 'Total candidates', value: df(data.length)},
        {key: 'Total parties', value: df(totalParties.length)},
        {key: 'Total male votes', value: df(maleVotes)},
        {key: 'Total female votes', value: df(femaleVotes)},
        {key: 'Total third gender votes', value: df(otherVotes)},
        {key: 'Total votes', value: df(totalVotes)},
        {key: 'Total polled votes', value: df(totalPolledVotes)}
    );

    return electionSummary;
}

export const getWinnersSummary = (data) => {
    const uniqueData = getUniqueDataByKey(data, 'party');
    const sortUniqueData = sortAscending(uniqueData, 'party');
    const winnersSummary = [...sortUniqueData.map(d => ({party: party(d), seats: getDataByPartyName(data, party(d)).length}))]
    return winnersSummary;
}

export const getWinnersData = (data) => {
    const consNumbers = getConsNumbers(data);
    const winnwersData = consNumbers.map((consNumber, index) => {
        const consData = getDescenConsInfo(data, consNumber);
        consData[0]['candidates'] = consData.length;
        consData[0]['winning_margin'] = xValueCandidateVotes(consData[0]) - xValueCandidateVotes(consData[1]);
        consData[0]['winning_margin_percentage'] = candidateVotes(consData[0]) - candidateVotes(consData[1]);
        return (
            // sort the cons data and get the first row
            consData[0]
        )
    });
    return winnwersData;
};

export const getConsDataForIndeCandidates = (data, winnersData) => {
    const allConsTopIndeDatas = []
    const consIndeData = winnersData.map((winnerData, index) => {        
        const consIndeDatas = data.filter(d => (getConsNumber(d) === getConsNumber(winnerData)) && (party(d) === 'Independent'));
        const sortConsIndeDatas = sortDescenting(consIndeDatas, 'candidate_total_votes');
        if(sortConsIndeDatas.length > 0) {
            allConsTopIndeDatas.push(sortConsIndeDatas[0]);
        }
        return true;
    });
    return allConsTopIndeDatas;
}

export const getFilterdDataByConsNum = (data, consNumbers) => 
    [...consNumbers.map((consNumber, index) => getConsInfoByConsNum(data, consNumber))].flat()


