import React, { useState } from 'react';

import './index.css';
import { GetCsvData } from './DataProcess/GetCsvData';
import Dropdown from './components/Dropdown';
import SubtitleH3 from './components/SubtitleH3';
import ElectionSummary from './components/ElectionSummary';
import ResultSummary from './components/ResultSummary';
import TextContent from './components/TextContent';
import DataName from './components/DateName';
import BarChart from './BarChart/BarChart';
import GeoChartWrapper from './GeoChart/GeoChartWrapper';
import DisclaimerSection from './components/DisclaimerSection';

import {
    allColors,
    barChartOptions, 
    getWinnersData,
    makeDistrictsDropdownData,
    makeWinPartiesDropdownData,
    getElectionSummary,
    getWinnersSummary
} from './DataProcess/RearrangeData';


// const csvUrl= 'tn_ac_2021.csv';
const csvUrl= 'https://raw.githubusercontent.com/baskicanvas/tn-ae-2021-result/gh-pages/tn_ac_2021.csv';
    
const App = () => {
    const data = GetCsvData(csvUrl);

    const [district, setDistrict] = useState('ALL DISTRICTS');
    const [chartOrder, setChartOrder] = useState('default');
    const [partyName, setPartyName] = useState('All Winning Parties');
    // console.log(district);
    // console.log(partyName);

    if(!data) {
        return <h1>Loading...</h1>
    }
    // 
    const winnersData = getWinnersData(data);
    const districtsDropdownData = makeDistrictsDropdownData(winnersData);
    const partiesDropdownData = makeWinPartiesDropdownData(winnersData);
    const electionSummary = getElectionSummary(data, winnersData);
    const resultSummary = getWinnersSummary(winnersData);

    return (
        <article>
            <DataName
                className='date-time'
            ></DataName>
            <header className='page-container'>
                <div>
                    <h1>
                        Tamil Nadu General Assembly Elections MAY-2021
                    </h1>
                    <div className='sub-header'>
                        Dravida Munnetra Kazhagam and its alliance have won the Tamil Nadu 16th legislative assembly election and
                        Mr. <b>M</b>uthuvel <b>K</b>arunanidhi <b>Stalin</b> has been elected as Chief Minister of Tamil Nadu.
                    </div>
                </div>
            </header>
        
            <div className='page-container'>
                <SubtitleH3
                    className='subtitle'
                    text={'Election Summary'}
                ></SubtitleH3>
                <ElectionSummary
                    electionSummary={electionSummary}
                ></ElectionSummary>

                <SubtitleH3
                    className='subtitle'
                    text={'Result Summary'}
                ></SubtitleH3>
                <ResultSummary
                    resultSummary={resultSummary}
                    allColors={allColors}
                    winnersData={winnersData}
                ></ResultSummary>

                <SubtitleH3
                    className='subtitle'
                    text={'Individual Constituency Details'}
                ></SubtitleH3>
                <main>
                    <section className='bar-chart'>
                        <TextContent
                            className='text-content'
                            textContent={
                                `Each bar represents the total votes in a constituency.
                                The coloured circles on the bar represents individual party votes 
                                and the circle radius is scaled with respect to the polled votes in that constituency.`
                            }
                        ></TextContent>
                        <div className='bar-chart-container'>
                            <Dropdown
                                id={'select-district'}
                                className={'select-district'}
                                defaultValue={'ALL DISTRICTS'}
                                options={districtsDropdownData}
                                defaultVale={district}
                                disabled={(partyName !== 'All Winning Parties' )}
                                onOptionChange={setDistrict}
                            ></Dropdown>
                            <Dropdown
                                id={'barchart-order'}
                                className={'barchart-order'}
                                defaultValue={'default'}
                                options={barChartOptions}
                                defaultVale={chartOrder}
                                disabled={false}
                                onOptionChange={setChartOrder}
                            ></Dropdown>
                            <Dropdown
                                id={'select-winning-party'}
                                className={'select-winning-party'}
                                defaultValue={'All Winning Parties'}
                                options={partiesDropdownData}
                                defaultVale={partyName}
                                disabled={(district !== 'ALL DISTRICTS')}
                                onOptionChange={setPartyName}
                            ></Dropdown>
                        </div>
                        <div id='wrapper' className='wrapper'>
                            <BarChart
                                data={data}
                                winnersData={winnersData}
                                district={district}
                                chartOrder={chartOrder}
                                partyName={partyName}
                            ></BarChart>
                        </div>
                        
                    </section>
                    <section className='geo-winner-chart'>
                        <GeoChartWrapper
                            consData={data}
                            winnersData={winnersData}
                            district={district}
                            partyName={partyName}
                        ></GeoChartWrapper>
                    </section>
                </main>
                <DisclaimerSection
                    className='text-content-small'
                ></DisclaimerSection>
            </div>
        </article>
    );
};

export default App;